import Vue from 'vue';
import {
	mapState
} from 'vuex';
import appHotspot from '../../../basic-component/app-hotspot/app-hotspot.vue';

export default {
	name: 'app-user-login',
	components: {
		appHotspot,
	},
	data() {
		return {};
	},
	computed: {
		openType() {



			return 'getUserInfo';
		},
		...mapState('mallConfig', {
			auth_page: state => state.auth_page,
		}),
		...mapState({
			showLoginModal: function(state) {
				return state.user.showLoginModal
			}
		}),
	},
	created() {
		const vm = this;
		Vue.use({
			install(Vue, options) {
				Vue.prototype.$layout = {
					getUserInfo() {
						vm.showLoginModal = true;
						return new Promise((resolve, reject) => {
							vm.getUserInfo = (e) => {};
						});
					},
				};
			},
		});
	},
	methods: {
		getUserProfileClick(e,userinfo) {
			console.log(userinfo,'cs-login');
			if(!userinfo){
				uni.navigateTo({
					url: '/plugins/newuser/index/index'
				})
				return;
			}

			this.$store.commit('user/showLoginModal', false);
			const resolve = this.$user.getUserInfoResolve;
			const reject = this.$user.getUserInfoReject;
			this.$event.on(this.$const.EVENT_USER_LOGIN, true).then(() => {
				this.$jump({
					open_type: 'reload'
				})
			});
			console.log('进来了');
			uni.getUserProfile({
				desc: '用于完善会员资料', // 声明获取用户个人信息后的用途，后续会展示在弹窗中，请谨慎填写
				success: (res) => {
					
					let obj = JSON.parse(res.rawData);
					obj.avatarUrl = userinfo.avatar;
					 obj.nickName = userinfo.nickname;
					 res.rawData = JSON.stringify(obj);
					 res.userInfo.nickName = '123';
					console.log(res)
					if (res.errMsg !== 'getUserProfile:ok') {
						this.$store.commit('user/showLoginModal', true);
						return reject(res.errMsg);
					} else {
						return resolve(res);
					}
				}
			})

		},
		link() {
			this.$store.commit('user/showLoginModal', false);
		},
		cancel() {
			this.$store.commit('user/showLoginModal', false);
			this.$user.getUserInfoReject('getUserInfo fail: cancel.');
			let pages = getCurrentPages();
			let list = ['/pages/index/index', '/pages/user-center/user-center'];

			let url = this.$platDiff.route();












			if (list.includes(url)) {

				url = this.$platDiff.routeWithOption();




				// uni.redirectTo({
				// 	url: url
				// });
			} else if (pages.length >= 2) {
				uni.navigateBack({
					delta: 1
				});
			} else {
				uni.redirectTo({
					url: '/pages/index/index'
				});
			}
		},

		getUserInfoClick(e) {



		},

		getUserInfo(e) {



























			this.$store.commit('user/showLoginModal', false);
			const resolve = this.$user.getUserInfoResolve;
			const reject = this.$user.getUserInfoReject;
			this.$event.on(this.$const.EVENT_USER_LOGIN, true).then(() => {
				this.$jump({
					open_type: 'reload'
				})
			});

			if (e.detail.errMsg !== 'getUserInfo:ok') {
				this.$store.commit('user/showLoginModal', true);
				return reject(e.detail.errMsg);
			} else {
				return resolve(e);
			}




















































		},













	}
}
